.section {
  position: relative;
  padding: 100px calc(50% - 470px);

  &__heading {
    left: 0;
    right: 0;
    top: -0.5em;
    margin: auto;
    width: max-content;
  }
}

@media (max-width: $small-screen-pointer) {
  .section {
    padding-left: 30px;
    padding-right: 30px;
  }
}
